body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Merriweather', serif;
  /* font-family: 'Vollkorn', serif; */
  /* font-family: 'Times New Roman'; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
    /* background-color: grey; */
    /* background-color: rgb(196, 30, 58, .9); */
    color: rgb(196, 30, 58);
}

.nav-link {
    color: rgb(196, 30, 58) !important;
}

.btn-primary {
    background-color: rgb(196, 30, 58);
    border-color: rgb(196, 30, 58);
}

.btn-primary:disabled {
    background-color: rgb(196, 30, 58);
    border-color: rgb(196, 30, 58);
}

.card-subtitle {
    color: black;
}

.card-body > a {
    color: rgb(196, 30, 58);
    margin: 1rem;
}

.swipecard {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.filterheader {
    color: rgb(196, 30, 58);
}

.homephotos {
    padding: 0;
    background-image: url(/static/media/housedining.4d5711b6.jpg);
    background-size: cover;
    background-attachment: fixed;
    height: calc(100vh - 56px);
    width: 100%;
}

.hometitle {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90%;
}

.hometitle > h2 {
    color: white;
    /* font-size: 8rem; */
    font-size: 16vw;
}

.homeinfo {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.homedescription {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.homedescription > .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.homedescription > .container > h3 {
    text-align: center;
    margin-top: 16px;
    -webkit-margin-after: 16px;
            margin-block-end: 16px;
}

.homedirections {
    display: flex;
    flex-direction: row;
}

#addswipebutton {
    width:100%;
    /* height:100%; */
}

#middashboard {
    margin: 0;
    padding: 0;
}

body {
    color: grey;
    /* background-color: rgb(196, 30, 58, .9); */
}

